





























































import Component from 'vue-class-component';
import Vue from 'vue';
import { ElForm } from 'element-ui/types/form';
import Permission from '@/components/Permission.vue';

@Component({
    components: {
        permission: Permission
    }
})
export default class AdminAdd extends Vue {
    public form = {
        adminName: '',
        userName: '',
        password: '',
        sex: 1,
        department: '',
        job: '',
        level: 'T1',
        phone: '',
        workNo: ''
    };
    public rules = {
        username: [
            { min: 5, message: '用户名至少得5个字符啊', trigger: 'change' }
        ],
        password: [
            { min: 6, max: 12, message: '密码必须6到12位', trigger: 'change' }
        ]
    };

    public created() {
        Object.keys(this.form).forEach(k => {
            if (k !== 'sex' && k !== 'level') {
                const rules = (<any>this.rules)[k] || [];
                rules.push({
                    required: true,
                    message: '必填项不能为空',
                    trigger: 'change'
                });
                if (k === 'phone') {
                    rules.push({
                        pattern: /^1[3456789]\d{9}$/,
                        message: '无效电话号',
                        trigger: 'change'
                    });
                }

                (<any>this.rules)[k] = rules;
            }
        });
    }

    public onSubmit() {
        const form = <ElForm>this.$refs.form;
        form.validate((valid: boolean) => {
            if (valid) {
                const now = Date.now();
                const data = Object.assign({}, this.form, {
                    createTime: now,
                    updateTime: now,
                    role: JSON.stringify(
                        (<Permission>this.$refs.permission).parse()
                    )
                });

                this.$http
                    .post('/api/admin/addAdmin', data, {
                        'Content-Type': 'application/json'
                    })
                    .then(() => {
                        this.$message.success('添加成功');
                        this.reset();
                    })
                    .catch(console.log);
            }
        });
    }

    public reset() {
        (<ElForm>this.$refs.form).resetFields();
    }
}
